import { useEffect, useState } from "react";
import {
  Truck,
  Package,
  Check,
  ChevronDown,
  ChevronUp,
  Shield,
  Clock,
  CreditCard,
} from "lucide-react";
import toast from "react-hot-toast";
import Header from "../ComponentChilds/Header";
import Footer from "../ComponentChilds/Footer";
import { motion, AnimatePresence } from "framer-motion";

export default function EnhancedCheckoutPage({ basketItemCount }) {
  const [formData, setFormData] = useState({
    fullName: "",
    address: "",
    city: "",
    province: "",
    postalCode: "",
    phone: "",
    email: "",
    deliveryNotes: "",
  });
  const [showOrderSummary, setShowOrderSummary] = useState(false);
  const [deliveryOption, setDeliveryOption] = useState("standard");
  const [orderItems, setOrderItems] = useState([]);
  const [discountCode, setDiscountCode] = useState("");
  const [discount, setDiscount] = useState(0); // store discount as state
  const [subTotal, setSubTotal] = useState(0);
  const [totalPrice, setTotalPrice] = useState(0);
  const discountCodes = ["off50", "summer", "new"];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  useEffect(() => {
    const basketItems =
      JSON.parse(localStorage.getItem("shoppingBasket")) || [];
    setOrderItems(basketItems);
  }, []);

  useEffect(() => {
    calculatePrices();
  }, [orderItems, discount]);

  const calculatePrices = () => {
    const subtotal = orderItems.reduce(
      (sum, item) => sum + item.price * item.quantity,
      0
    );
    setSubTotal(subtotal);
    const total = subtotal - subtotal * discount;
    setTotalPrice(total);
  };

  const handleDiscount = () => {
    if (discountCodes.includes(discountCode.trim().toLowerCase())) {
      setDiscount(0.1); // 10% discount
      toast.success("کد تخفیف با موفقیت اعمال شد 🎉");
    } else {
      setDiscount(0);
      toast.error("کد تخفیف معتبر نیست ❌");
    }
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat("fa-IR").format(price) + " تومان";
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    alert("در حال انتقال به درگاه پرداخت...");
    handleSubmitOrder(orderItems);
  };

  function handleSubmitOrder(items) {
    const orderData = {
      order_id: 1,
      order_date: new Date().toLocaleString(),
      fullName: formData?.fullName,
      customer: 1,
      address: formData?.address,
      city: formData?.city,
      province: formData?.province,
      code: formData?.postalCode,
      phone: formData?.phone,
      items: orderItems,
      email: formData?.email || "",
      delivery_notes: formData?.deliveryNotes || "",
      total: totalPrice,
    };
  }

  const discountAmount = subTotal * discount;
  return (
    <>
      <Header basketItemCount={basketItemCount} />
      <div className="bg-gray-50 min-h-screen py-8">
        <div className="container mx-auto px-4 max-w-6xl">
          <h1 className="text-3xl font-bold text-center mb-8 text-gray-800">
            تکمیل خرید
          </h1>

          {/* Progress bar */}
          <div className="mb-10">
            <div className="flex justify-between items-center mb-2">
              <span className="font-medium text-blue-600">سبد خرید</span>
              <span className="font-medium text-blue-600">اطلاعات ارسال</span>
              <span className="font-medium text-gray-400">پرداخت</span>
              <span className="font-medium text-gray-400">تکمیل سفارش</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div className="bg-blue-600 h-2 rounded-full w-1/2"></div>
            </div>
          </div>

          <div className="md:flex md:gap-8">
            {/* Checkout Form */}
            <div className="md:w-2/3">
              <form
                onSubmit={handleSubmit}
                className="bg-white rounded-lg shadow-lg p-6 mb-6 border border-gray-100"
              >
                <h2 className="text-xl font-semibold mb-6 flex items-center text-gray-800 pb-2 border-b">
                  <Truck size={20} className="ml-2 text-blue-600" />
                  اطلاعات ارسال
                </h2>

                <div className="grid md:grid-cols-2 gap-5 mb-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      نام و نام خانوادگی <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="fullName"
                      value={formData.fullName}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="نام و نام خانوادگی گیرنده"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      شماره موبایل <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="tel"
                      name="phone"
                      value={formData.phone}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="09123456789"
                      required
                    />
                  </div>

                  <div className="md:col-span-2">
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      ایمیل
                    </label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="example@email.com"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      استان<span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="city"
                      value={formData.province}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="نام استان"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      شهر <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="city"
                      value={formData.city}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="نام شهر"
                      required
                    />
                  </div>

                  <div className="md:col-span-2">
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      آدرس کامل <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="address"
                      value={formData.address}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="آدرس دقیق پستی"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      کد پستی <span className="text-red-500">*</span>
                    </label>
                    <input
                      type="text"
                      name="postalCode"
                      value={formData.postalCode}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      placeholder="1234567890"
                      required
                    />
                  </div>

                  <div className="md:col-span-2">
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      توضیحات ارسال (اختیاری)
                    </label>
                    <textarea
                      name="deliveryNotes"
                      value={formData.deliveryNotes}
                      onChange={handleChange}
                      className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                      rows="2"
                      placeholder="اگر توضیح خاصی برای پست دارید اینجا بنویسید"
                    ></textarea>
                  </div>
                </div>

                {/* Delivery Options */}
                <div className="mb-8">
                  <h3 className="text-lg font-medium mb-3 text-gray-800">
                    روش ارسال
                  </h3>
                  <div className="space-y-3">
                    <label
                      className={`block p-4 border rounded-lg cursor-pointer transition-all ${
                        deliveryOption === "standard"
                          ? "border-blue-500 bg-blue-50"
                          : "border-gray-200 hover:border-blue-200"
                      }`}
                    >
                      <div className="flex items-start">
                        <input
                          type="radio"
                          name="deliveryOption"
                          value="standard"
                          checked={deliveryOption === "standard"}
                          onChange={() => setDeliveryOption("standard")}
                          className="mt-1"
                        />
                        <div className="mr-2 flex-1">
                          <div className="flex justify-between">
                            <span className="font-medium">باربری</span>
                            <span className="font-medium text-gray-800">
                              هزینه ی ارسال به عهده ی مشتری
                            </span>
                          </div>
                          <p className="text-gray-500 text-sm flex items-center mt-1">
                            <Clock size={14} className="ml-1" />
                            تحویل 3 تا 10 روز کاری
                          </p>
                        </div>
                      </div>
                    </label>
                  </div>
                </div>

                {/* Trust badges */}
                <div className="grid grid-cols-3 gap-2 my-6 text-center">
                  <div className="p-3">
                    <div className="flex justify-center mb-2">
                      <Shield size={24} className="text-blue-600" />
                    </div>
                    <p className="text-xs text-gray-600">پرداخت امن</p>
                  </div>
                  <div className="p-3">
                    <div className="flex justify-center mb-2">
                      <Package size={24} className="text-blue-600" />
                    </div>
                    <p className="text-xs text-gray-600">تضمین اصالت کالا</p>
                  </div>
                  <div className="p-3">
                    <div className="flex justify-center mb-2">
                      <Clock size={24} className="text-blue-600" />
                    </div>
                    <p className="text-xs text-gray-600">تحویل سریع</p>
                  </div>
                </div>
              </form>
            </div>

            {/* Order Summary */}
            <div className="md:w-1/3">
              <div className="bg-white rounded-lg shadow-lg p-6 sticky top-6 border border-gray-100">
                {/* خلاصه سفارش */}
                <div className="flex justify-between items-center mb-6">
                  <h2 className="text-xl font-semibold text-gray-800 flex items-center">
                    <Package size={20} className="ml-2 text-blue-600" />
                    خلاصه سفارش
                  </h2>
                  <button
                    onClick={() => setShowOrderSummary(!showOrderSummary)}
                    className="md:hidden text-gray-500"
                  >
                    {showOrderSummary ? (
                      <ChevronUp size={20} />
                    ) : (
                      <ChevronDown size={20} />
                    )}
                  </button>
                </div>

                <div
                  className={`${
                    showOrderSummary ? "block" : "hidden"
                  } md:block`}
                >
                  {/* Order Items */}
                  <div className="mb-6 space-y-4">
                    {orderItems.map((item) => (
                      <div
                        key={item.id}
                        className="flex items-center border-b pb-4"
                      >
                        <img
                          src={item.imageUrl}
                          alt={item.productName}
                          className="w-16 h-16 rounded object-cover border border-gray-200"
                        />
                        <div className="mr-3 flex-1">
                          <p className="font-medium text-gray-800">
                            {item.productName}
                          </p>
                          <p className="text-sm text-gray-500">
                            تعداد: {item.quantity}
                          </p>
                          <p className="font-medium text-gray-900 mt-1">
                            {formatPrice(item.price)}
                          </p>
                        </div>
                      </div>
                    ))}
                  </div>

                  {/* Discount input */}
                  <div className="mb-6 border-b pb-6">
                    <div className="flex items-end gap-2">
                      <div className="flex-1">
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          کد تخفیف
                        </label>
                        <input
                          type="text"
                          name="discountCode"
                          placeholder="کد تخفیف خود را وارد کنید"
                          value={discountCode}
                          onChange={(e) => setDiscountCode(e.target.value)}
                          className="w-full px-4 py-3 border border-gray-300 outline-none rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-all"
                        />
                      </div>
                      <button
                        type="button"
                        className="px-4 py-3 bg-gray-100 hover:bg-gray-200 text-gray-800 font-medium rounded-lg transition-colors"
                        onClick={handleDiscount}
                      >
                        اعمال
                      </button>
                    </div>
                  </div>

                  {/* Price Summary */}
                  <div className="space-y-3 mb-6 text-sm">
                    <div className="flex justify-between">
                      <p className="text-gray-600">جمع محصولات</p>
                      <p className="font-medium">{formatPrice(subTotal)}</p>
                    </div>

                    {/* تخفیف با انیمیشن */}
                    <AnimatePresence>
                      {discount > 0 && (
                        <motion.div
                          className="flex justify-between text-green-600"
                          initial={{ opacity: 0, height: 0 }}
                          animate={{ opacity: 1, height: "auto" }}
                          exit={{ opacity: 0, height: 0 }}
                          transition={{ duration: 0.4 }}
                        >
                          <p>تخفیف</p>
                          <p className="font-medium">
                            {formatPrice(subTotal * discount)}
                          </p>
                        </motion.div>
                      )}
                    </AnimatePresence>

                    <div className="flex justify-between font-bold text-lg border-t pt-3 mt-3">
                      <p>جمع کل</p>
                      <p>{formatPrice(totalPrice)}</p>
                    </div>
                  </div>

                  {/* Submit */}
                  <button
                    type="submit"
                    onClick={handleSubmit}
                    className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-4 px-6 rounded-lg flex items-center justify-center transition-colors shadow-md hover:shadow-lg"
                  >
                    <CreditCard size={18} className="ml-2" />
                    ادامه به صفحه پرداخت
                  </button>

                  {/* Secure payment */}
                  <div className="mt-6 text-center">
                    <div className="flex justify-center mb-2">
                      <img
                        src="/api/placeholder/180/40"
                        alt="Payment methods"
                        className="h-8"
                      />
                    </div>
                    <p className="text-xs text-gray-500 flex items-center justify-center">
                      <Shield size={12} className="ml-1" />
                      پرداخت امن با استفاده از رمزنگاری SSL
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}
